.button {
  display: flex;
  justify-content: center;
  align-items: center;
  line-height: 1;
  width: 100%;
  min-width: 48px;
  height: 48px;
  margin: 0;
  padding: 0 16px;
  border-radius: 4px;
  background-color: transparent;
  font-weight: 600;
  font-size: 16px;
  text-decoration: none;
  cursor: pointer;

  &:hover {
    border-color: currentColor;
  }
}

.button--filled {
  border-color: transparent;
}