.main {
  margin: 0 auto;
  height: calc(100vh - 112px);
  min-width: 280px;
  display: flex;
  flex-wrap: wrap;

  .background {
    background-image: url(../../assets/background.jpg);
    background-repeat: no-repeat;
    min-width: 280px;
    margin: 0;
    flex: 4;

    .textbox {
      margin-top: 150px;
      margin-right: 26px;
      padding-bottom: 26px;

      .title {
        color: #FDD016;
        text-transform: uppercase;
        text-align: right;
        font-size: 22px;
        font-family: 'Rubik', sans-serif;
        opacity: 1;

        @media (max-width: 992px) {
          padding: 6rem 0 0 0;
          text-align: center;
        }

        @media (max-width: 768px) {
          font-size: 18px;
          letter-spacing: 3.5px;
        }

        @media (max-width: 420px) {
          font-size: 14px;
        }

      }

      .text {
        margin-top: 30px;
        color: #DFDFDF;
        font-size: 22px;
        line-height: 30px;
        text-align: right;
        font-family: 'Rubik', sans-serif;
        font-weight: lighter;
        opacity: 1;

        @media (max-width: 992px) {
          padding: 2rem 0 0 0;
          text-align: center;
        }

        @media (max-width: 768px) {
          font-size: 18px;
        }

        @media (max-width: 420px) {
          font-size: 15px;
        }
      }
    }
  }

  .calendar {
    flex: 2;
    display: flex;
    justify-content: center;
    background-color: #222324;
  }
}

